@import "../base/_variables.sass";

//Heights
.viewHeight100 {
  height: 100vh; }

// Paddings
.containerTopPadding {
  padding-top: 20px; }

.noPadding {
  padding: 0; }

//Home Page - Our Work Grid System

.homeOurWorkRow {
  position: relative; }

.homeOurWorkGridOne {
  height: 70%;
  position: absolute;
  left: 0;
  top: 0;
  &:hover .homeOurWorkTextWrapper {
    opacity: 1; }
  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 10;
    background-color: rgba(0,0,0,0.2); } }

.homeOurWorkGridTwo {
  height: 50%;
  position: absolute;
  right: 0;
  top: 0;
  &:hover .homeOurWorkTextWrapper {
    opacity: 1; } }

.homeOurWorkGridThree {
  height: 30%;
  position: absolute;
  left: 25%;
  top: 70%;
  &:hover .homeOurWorkTextWrapper {
    opacity: 1; } }

.homeOurWorkGridFour {
  height: 30%;
  position: absolute;
  left: 0;
  top: 70%;
  &:hover .homeOurWorkTextWrapper {
    opacity: 1; } }

.homeOurWorkGridFive {
  height: 50%;
  position: absolute;
  right: 0;
  top: 50%;
  &:hover .homeOurWorkTextWrapper {
    opacity: 1; } }

.homeOurWorkTextWrapper {
  opacity: 0;
  transition: 0.5s ease-in-out; }
